<template>
  <div class="customer-content">
    <el-form inline class="form">
      <el-form-item label="套餐名称">
        <el-input
          v-model="initData.name"
          @change="getList"
          class="input-name"
          placeholder="请输入名称"
          clearable
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getList">搜索</el-button>
        <el-button type="primary" @click="handleOperatePackage('add', '')"
          >新增套餐</el-button
        >
      </el-form-item>
    </el-form>
    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 282px)">
        <el-table-column
          prop="packageName"
          label="套餐名称"
          align="center"
          width="250px"
        ></el-table-column>
        <el-table-column
          prop="packageMinutes"
          label="套餐分钟数"
          width="230px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="packageInsideUnitPrice"
          label="套餐内话费单价元/分钟"
          width="220px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="packageOutsideUnitPrice"
          label="套餐外话费单价元/分钟"
          width="220px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="seatFee"
          label="坐席费用元/位"
          width="220px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="midNumberFee"
          label="中间号月租元/月"
          width="220px"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="smallNumberFee"
          label="小号月租元/月"
          width="220px"
          align="center"
        ></el-table-column>
        <el-table-column
          align="center"
          fixed="right"
          label="操作"
          width="180px"
          class-name="operation-group"
        >
          <template #default="{ row }">
            <el-link
              type="primary"
              :underline="false"
              @click="handleOperatePackage('edit', row)"
              >编辑</el-link
            >
            <el-link
              type="primary"
              :underline="false"
              @click="handleLookDetail(row)"
              >操作记录</el-link
            >
          </template>
        </el-table-column>
        <template #empty>
          <TableEmpty empty-text="暂无明细"></TableEmpty>
        </template>
      </el-table>
      <!--   分页   -->
      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
    <!-- 新增/编辑套餐-->
    <AddPackageDrawer
      v-model:visible="initData.addPackageVisible"
      :info="initData.info"
      :isEditing="initData.isEditing"
      @emitFunc="getList"
    ></AddPackageDrawer>
    <!-- 操作详情-->
    <OperationRecordDialog
      v-model:visible="initData.detailVisible"
      :packageSettingId="initData.packageSettingId"
    ></OperationRecordDialog>
  </div>
</template>
<script lang="ts" setup>
import { shallowReactive, defineAsyncComponent } from "vue";

import { LocalStorage, usePage } from "jz-tool-lib";

import { customerSettingsApi } from "@api/customer-management-api";
defineOptions({
  name: "businessManagement.ospreyCloudCall.subCustomerPackage",
});
//新增/编辑套餐
const AddPackageDrawer = defineAsyncComponent(
  () => import("./components/add-package-drawer.vue")
);
//操作记录
const OperationRecordDialog = defineAsyncComponent(
  () => import("./components/operation-record-dialog.vue")
);
const agentId = LocalStorage.getItem("bd_agentId");
const initData = shallowReactive({
  name: "",
  addPackageVisible: false,
  detailVisible: false,
  isEditing: false,
  info: "",
  packageSettingId: "",
  id: "",
});
const { getList, pageNumberChange, pageSizeChange, pageData } = usePage({
  listApi: customerSettingsApi,
  props: {
    pageSize: "pageSize",
    pageNumber: "page",
  },
  searchParams() {
    return {
      agencyId: agentId,
      packageName: initData.name,
    };
  },
});

//新增套餐
function handleOperatePackage(action: "add" | "edit", row) {
  initData.info = row;
  if (action === "edit" && row) {
    initData.isEditing = true;
  } else {
    initData.isEditing = false;
  }
  initData.addPackageVisible = true;
}
// 查看详情
function handleLookDetail(row) {
  initData.packageSettingId = row.id;
  initData.detailVisible = true;
}
</script>
<style lang="scss" scoped>
.form {
  display: flex;
  justify-content: flex-end;
}
.input-name {
  width: 200px;
}
</style>
